import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertService } from '../../../../core/services/alert.service';
import { ToastMessageService } from '../../../../core/services/toast-message.service';
import { AppUtility } from '../../../../core/services/utility.services';
import { LookupService } from '../../../../shared/http/lookup.service';
import { BOMExplosionModel } from '../../models/bom-explosion.model';
import { BOMExplosionService } from '../../http/bom-explosion.service';
//import { CustomValidator } from 'src/app/core/services/custom.validators';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import * as moment from 'moment';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';

@Component({
  selector: 'app-bom-explosion',
  templateUrl: './bom-explosion.component.html',
  styleUrls: ['./bom-explosion.component.scss']
})
export class BomExplosionComponent implements OnInit {
  public mbloaderEnabled: boolean = false;
  public mbIsLoading: boolean = false;
  public msLookupField: string = '';
  public marrLookupGridData: any = [];
  public mobjLookupKeyData: any = {};
  public mbIsLookupModal: boolean = false;
  public msCustomDateFormat: string = "";
  public mObjModel: BOMExplosionModel = new BOMExplosionModel();
  public mObjBlankModel: BOMExplosionModel = new BOMExplosionModel();
  public msScreenName: string = 'BOM Explosion';

  public msItemFromDesc: string = "";
  public msItemToDesc: string = "";
  public msLocationFromDesc: string = "";
  public msLocationToDesc: string = "";

  public mbChange: boolean = false;
  //BOM Explosion--S
  public mbBomExplosionDialog: boolean = false;
  public marrTreeList: any = [];
  public marrDrillDownList: any = [];
  public miQtyPrecision: number = 0;
  public miQtyPrecisionFormat: string = 'n' + this.miQtyPrecision;
  public topHeaderLeftLogoUrl: string = "";
  public topHeaderLeftName: string = "";
  public topHeaderLeftPoratlName: string = "";
  public currentDateTime: string = "";

  public mbwhereUsedView: boolean = false;
  public mobjRowData: any = {};
  
  public isRender: boolean = false;
  //BOM Explosion--E

  public mArrLoopItemGrid: any = [];
  public mbItemLoopDialog: boolean = false; 


  constructor(
    private objService: BOMExplosionService,
    private activeRoute: ActivatedRoute,
    private objAlertService: AlertService,
    private objToastService: ToastMessageService,
    private objUtils: AppUtility,
    private objLookupService: LookupService,
    public router: Router,
    public userFieldService: UserFieldService,
    private dateFormatService: DateFormatService,
    private lsService: LocalStorageService
    ) 
    { 
      this.bmlfGetDrillDownList();
    }

  ngOnInit(): void {
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();

    let parrRoundingDefaults = JSON.parse(localStorage.getItem("RoundingDefaults"));
    this.miQtyPrecision = Number(parrRoundingDefaults[0].Default_Value);
    this.miQtyPrecisionFormat = 'n' + this.miQtyPrecision;
        
  }
  //----------------------------------------------------------------------------------------
  bmlfGetDrillDownList() {
    let pobjReqData = {
        ScreenName: 'ApplyPaymentComponent',
        LookUpKey: 'DrillDwnList',
        Filter1: "BOM Explosion"
    }
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
        if (res.LookupData.length > 0) {
            this.marrDrillDownList = res.LookupData;
            this.isRender = true;
        }
    });
}
  //----------------------------------------------------------------------------------------
  bmlfRefreshForm(){
    this.mObjModel = Object.assign({}, this.mObjBlankModel);
    this.msItemFromDesc = '';
    this.msItemToDesc = '';
    this.msLocationFromDesc = '';
    this.msLocationToDesc = '';
  }
  
  //----------------------------------------------------------------------------------------
  //Lookup Modal ---S
  bmlfOpenLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, psTitle = "", pnumRowIdx = null) {
    
    this.mbIsLoading = true;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    if (psTitle != "") {
      if (psField == "Location") {
        this.mobjLookupKeyData.title =  this.objUtils.bmgfonTranslate(psTitle);
      }else{
        this.mobjLookupKeyData.title = this.objUtils.bmgfonTranslate(psTitle);
      }
    } else {
      this.mobjLookupKeyData.title = psTitle;
    }
    if (psEvent == 'blur' && (psValue == undefined || psValue == '' || this.msLookupField == psValue)) {
      this.mbIsLoading = false;
      this.msLookupField = '';
      if (psValue == undefined || psValue == '') {
        this.mObjModel[psField] = "";
      }
      return false;
    }

    let ptmpLookupLocationKey: string = '';
    let ptmpLookupItemKey: string = '';

    if(this.mObjModel.Option == 'BOM Explosion'){
      ptmpLookupLocationKey = 'BOMExpLocation';
      ptmpLookupItemKey = 'BOMExpItem';
    }else{
      ptmpLookupLocationKey = 'BOMExpWhereUsedLocation';
      ptmpLookupItemKey = 'BOMExpWhereUsedItem';
    }
    let psReqData: any
    
    if(psField == "LocationFrom"){
        psReqData = { "ScreenName": this.msScreenName, "LookUpKey": ptmpLookupLocationKey, "Filter1":  "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
    } else if (psField == "LocationTo"){
        psReqData = { "ScreenName": this.msScreenName, "LookUpKey": ptmpLookupLocationKey, "Filter1":  "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
    } else if(psField == "ItemFrom"){
        psReqData = { "ScreenName": this.msScreenName, "LookUpKey": ptmpLookupItemKey, "Filter1":  "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
    } else if(psField == "ItemTo"){
        psReqData = { "ScreenName": this.msScreenName, "LookUpKey": ptmpLookupItemKey, "Filter1":  "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
    }

      this.mbIsLoading = true;
      this.objLookupService.getLookup(psReqData).subscribe(res => {
        setTimeout(() => {
          this.mbIsLoading = false;
        }, 500);
        if (res == null) {
          this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
          return false;
        }
        if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
          let psLookupHeading = psField;
          res.LookupData.forEach(ele => {

            Object.keys(ele).forEach((key) => {
              let psKey = key.toUpperCase();
              if (psKey.search('DATE') != -1 || psKey.search('PURCHASEDT') != -1 || psKey.search('RECEIPTDT') != -1) {
                if (ele[key] && ele[key] != '') {
                  ele[key] = moment(ele[key]).format(this.msCustomDateFormat.toUpperCase());
                } else {
                  ele[key] = moment().format(this.msCustomDateFormat.toUpperCase());
                }
              }
            })
          });

         
          this.marrLookupGridData = res.LookupData;
          this.mobjLookupKeyData.field = psLookupHeading;
          //this.mobjLookupKeyData.field = psField;
          this.mobjLookupKeyData.gridData = this.marrLookupGridData;
          this.mobjLookupKeyData.type = psType;
          this.mobjLookupKeyData.eleId = psEleId;

          if (psValue) {
            this.mobjLookupKeyData.searchValue = String(psValue);
          } else {
            this.mobjLookupKeyData.searchValue = '';
          }

          if (psEvent == 'blur' && psValue) {
            let parrFilterdArr: any = [];
            /** --- Check : enter key matching record found. if find single row then direct call save lookup function --*/
            let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
            parrFilterdArr = this.marrLookupGridData.filter(obj => {
              if (String(obj[pobjFirstInput]).toUpperCase() == String(psValue).toUpperCase()) {
                return obj;
              }
            });
            if (parrFilterdArr.length == 1) {
              this.mobjLookupKeyData.value = parrFilterdArr[0];
              this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
              this.bmlfSaveLookupField(this.mobjLookupKeyData);
              this.mbIsLookupModal = false;
            } else {
              this.mbIsLookupModal = true;
            }
          }
          else if (psEvent == 'click') {
            this.mbIsLookupModal = true;
          }
        }
        else {
          this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
        }
      }, err => {
        console.log(err);
        this.mbIsLoading = false;
      })
    
    
}

  bmlfOpenErrorBlock(psMsg): void {
    this.objToastService.notifyError(this.objUtils.bmgfonTranslate(psMsg));
  }
  //------------------------------------------------------
  bmlfCloseLookupModal(pobjData): void {
    this.mbIsLookupModal = pobjData.isLookupModal;
    if(pobjData.eleId != null) {
      let psInputKey = this.bmlfGetInputColumn(pobjData.field);
      let parrFilterData: any = [];
      if (psInputKey) {
        parrFilterData = pobjData.gridData.filter(obj => obj[psInputKey].toUpperCase() == (pobjData.searchValue != null ? pobjData.searchValue.toUpperCase() : ''));
      }
      
      if (parrFilterData.length == 0) {
        this.mObjModel[pobjData.field] = '';
        if (pobjData.field == 'ItemFrom'){
          this.msItemFromDesc = ''; 
        } else if(pobjData.field == 'ItemTo') {
          this.msItemToDesc = ''; 
        } else if (pobjData.field == 'LocationFrom'){
          this.msLocationFromDesc = ''; 
        } else if(pobjData.field == 'LocationTo') {
          this.msLocationToDesc = ''; 
        }
  
      }
      document.getElementById(pobjData.eleId).focus();
    }
    

  }
  //------------------------------------------------------
  bmlfSaveLookupField(pobjEvent) {
    
    this.mbIsLoading = false;
    this.mbIsLookupModal = false;
    this.mbChange = true;
  
    if(pobjEvent.field == "LocationFrom"){
      if(pobjEvent.valueKey > this.mObjModel.LocationTo && this.mObjModel.LocationTo != ''){
        this.objToastService.notifyError(this.objUtils.bmgfonTranslate('BOMEXPLOSION.MSG.LOCATIONCHECK'));
        return false;
      }else{
        this.mObjModel.LocationFrom = pobjEvent.valueKey;
        this.msLocationFromDesc = pobjEvent.value.Description;
      }
    }else if(pobjEvent.field == "LocationTo"){
      if(pobjEvent.valueKey < this.mObjModel.LocationFrom && this.mObjModel.LocationFrom != ''){
        this.objToastService.notifyError(this.objUtils.bmgfonTranslate('BOMEXPLOSION.MSG.LOCATIONCHECK'));
        return false;
      }else{
        this.mObjModel.LocationTo = pobjEvent.valueKey;
        this.msLocationToDesc = pobjEvent.value.Description;
      }
    }else if(pobjEvent.field == "ItemFrom"){
      if(pobjEvent.valueKey > this.mObjModel.ItemTo && this.mObjModel.ItemTo != ''){
        this.objToastService.notifyError(this.objUtils.bmgfonTranslate('BOMEXPLOSION.MSG.ITEMCHECK'));
        return false;
      }else{
        this.mObjModel.ItemFrom = pobjEvent.valueKey;
        this.msItemFromDesc = pobjEvent.value.Description;
      }
    }else if(pobjEvent.field == "ItemTo") {
      if(pobjEvent.valueKey < this.mObjModel.ItemFrom && this.mObjModel.ItemFrom != ''){
        this.objToastService.notifyError(this.objUtils.bmgfonTranslate('BOMEXPLOSION.MSG.ITEMCHECK'));
        return false;
      }else{
        this.mObjModel.ItemTo = pobjEvent.valueKey;
        this.msItemToDesc = pobjEvent.value.Description;
      }
    }
  }
  //-------------------------------------------------------
  // bmlfOnGlobalFocus(field, value) {
  //   if (typeof value === 'object' && value.constructor === Object) {
  //       let valueCopy = Object.assign({}, value);
  //       this.msLookupField = valueCopy[field];
  //   } else {
  //       this.msLookupField = value;
  //   }
  // }  
  //-------------------------------------------------------
  bmlfGetInputColumn(psField) {
    if (psField == 'ItemFrom' || psField == 'ItemTo') {
      if(this.mObjModel.Option == 'BOM Explosion'){
        return 'Assembly_Item_Key'
      }else{
        return 'ItemKey'
      }
      
    }else if (psField == 'LocationFrom' || psField == 'LocationTo') {
      if(this.mObjModel.Option == 'BOM Explosion'){
        return 'Assembly_Location'
      }else{
        return 'Location'
      }
      
    }
   
  }
  //Lookup Modal ---E
  //----------------------------------------------------------------------------------------
  bmlfOnChange(fild){
    if(fild == 'LocationFrom' && this.mObjModel.LocationFrom == ''){
      this.msLocationFromDesc = '';
    } else if(fild == 'LocationTo' && this.mObjModel.LocationTo == ''){
      this.msLocationToDesc = '';
    } else if(fild == 'ItemFrom' && this.mObjModel.ItemFrom == ''){
      this.msItemFromDesc = '';
    } else if (fild == 'ItemTo' && this.mObjModel.ItemTo == ''){
      this.msItemToDesc = '';
    }

  }
  //----------------------------------------------------------------------------------------
  // So Footer Action finction
  bmlfFooterActionFn(event) {
  
    if (event == 'addEvn') {//New
      this.bmlfRefreshForm();
    } else if (event == 'closeEvn') {//Close
      this.bmlfonClose();
    } else if (event == 'processEvn') {//Close
      this.bmlfonBOMExplosion();
    }

  }
  //-------------------------------------------------------------------------------------
  bmlfonClose(){
    this.router.navigate(['/main/dashboard/dashboardpage']);
  }
  //-------------------------------------------------------------------------------------
  bmlfonBOMExplosion(){

    if(this.mObjModel.Option.toUpperCase() == 'BOM EXPLOSION'){
      this.bmlfGetBOMExplosionData();
    }else{
      this.bmlfGetWhereUsedData();
    }
    
  }
  //-------------------------------------------------------------------------------------
  bmlfCloseExplosionModal() {
    this.mbBomExplosionDialog = false;
  }
  //-------------------------------------------------------------------------------------
  bmlfGetBOMExplosionData(): void {
   
    this.mbIsLoading = true;
    let pobjOData = { HeaderData: [], Response: [] };
    pobjOData.HeaderData.push({
        "FromItem": this.mObjModel.ItemFrom,
        "ToItem": this.mObjModel.ItemTo,
        "FromLocation": this.mObjModel.LocationFrom,
        "ToLocation": this.mObjModel.LocationTo,
        "SingleLevel": this.mObjModel.Level == 'Single Level' ? true : false
    });
    pobjOData.Response.push({
        "ResponseText": "",
        "ErrorCode": ""
    })

    let psJData = JSON.stringify(pobjOData);
    let psTempjData = JSON.stringify(psJData);
    let psJson = '{"JSON":' + psTempjData + '}';
    this.objService.bmlfGetBomExplosion(psJson).subscribe(
        res => {
            if (res != null && res.length > 0) {
              //let tmpArr = res.filter(obj => obj["TreeValue"] == 0);
                res.forEach(element => {
                  if(element.Treelevel == 0){
                    element.QtyRequired = (element.QtyRequired == 0 ? 1 : element.QtyRequired) ;
                  }
                });
                this.marrTreeList = res;
                this.mbBomExplosionDialog = true;
            }else if(res.length == 0){
              this.objToastService.notifyInfo(this.objUtils.bmgfonTranslate('BOMEXPLOSION.MSG.NORECORDSTOSHOW'));
            }
        }
    ).add(() => {
        this.mbIsLoading = false;
    })

 }
//-------------------------------------------------------------------------------------
  bmlfGetWhereUsedData() {
    this.mbwhereUsedView = true;
    this.mobjRowData = {};
    this.mobjRowData.FromItem = this.mObjModel.ItemFrom;
    this.mobjRowData.ToItem = this.mObjModel.ItemTo;
    this.mobjRowData.FromLocation = this.mObjModel.LocationFrom;
    this.mobjRowData.ToLocation = this.mObjModel.LocationTo;
    this.mobjRowData.Level = (this.mObjModel.Level == 'Single Level' ? true : false);
}
//-------------------------------------------------------------------------------------
  bmlfCloseWhereUsed(event) {
    this.mbwhereUsedView = event.option;
  }
//-------------------------------------------------------------------------------------
bmlfBOMloopEvent(){
  let pobjReqData = {
    ScreenName: 'BOM Explosion',
    LookUpKey: (this.mObjModel.Option == 'BOM Explosion' ? 'DetectLoopMPS' : 'DetectLoopMRP' )
}
this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
  if (res == null) {
    this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
    return false;
  } else if (res.LookupData.length == 1) {
    if(res.LookupData[0].ItemKey == '' && res.LookupData[0].Location == ''){
      this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
      return false;
    }else{
      this.mArrLoopItemGrid = res.LookupData;
      this.mbItemLoopDialog = true;
    }
    
  } else if (res.LookupData.length > 0) {
      this.mArrLoopItemGrid = res.LookupData;
      this.mbItemLoopDialog = true;
  } else {
    this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
    return false;
  }
});
}
//-------------------------------------------------------------------------------------
bmlfCloseItemLoopModal(){
  this.mbItemLoopDialog = false;
}
//-------------------------------------------------------------------------------------
  bmlfViewHelpDoc() {
    window.open('../../../../assets/help files/BOM Explosion.pdf', '_blank');
  }
//-------------------------------------------------------------------------------------
  bmlfOptionChange(){
    this.mObjModel.ItemFrom = '';
    this.msItemFromDesc = '';
    this.mObjModel.ItemTo = ''; 
    this.msItemToDesc = '';
    this.mObjModel.LocationFrom = '';
    this.msLocationFromDesc = '';
    this.mObjModel.LocationTo  = '';
    this.msLocationToDesc = '';
  }
//-------------------------------------------------------------------------------------


//-------------------------------------------------------------------------------------
}
