import { Component, OnInit, ViewChild, ElementRef, AfterViewInit } from '@angular/core';
import { GridDataResult, SelectionEvent } from "@progress/kendo-angular-grid";
import { Location } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import * as moment from 'moment';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { VersionModel } from '../../models/compare-prior-present.model';
import { ComparePriorPresentConstants } from '../../constant/compare-prior-present.const';
import { ViewPriorVersionService } from '../../http/view-prior-version.service';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { LookupService } from 'src/app/shared/http/lookup.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { CommonConstants } from 'src/app/core/constants/common.const';

@Component({
  selector: 'app-bom-compare-prior-present',
  templateUrl: './bom-compare-prior-present.component.html',
  styleUrls: ['./bom-compare-prior-present.component.scss']
})
export class BomComparePriorPresentComponent implements OnInit {
  public mObjModel: VersionModel = new VersionModel();
  public mObjModelBlank: VersionModel = new VersionModel();
  public mobjLookupDataModel: VersionModel = new VersionModel();

  public mObjPriorModel: VersionModel = new VersionModel();
  public mObjPriorModelBlank: VersionModel = new VersionModel();
  public mbSearchEvent: boolean = false;
  public mbDisabledPrimaryKey: boolean = false;
  public mbChange: boolean = false;
  public mbShowDialog: boolean = false;
  public mbloaderEnabled: boolean = false;
  public mbShowDeleteDialog: boolean = false;
  public msCallFor: string = "";
  public mbIsLoading: boolean = false;
  public marrLookupGridData: any = [];
  public mobjLookupKeyData: any = {};
  public mnumLineItemRowIndex: any = null;
  public mbIsLookupModal: boolean = false;
  public gridDataList = [];
  public msCopyRecord: string = "N";
  public msScreenName: string = 'BOM Compare Prior With Present';
  public msLookupField: string = '';

  public msRoutePath: string = "/main/dashboard/dashboardpage";
  public mbResult: boolean = false;

  @ViewChild('custom') customCtrl;
  public mbCustomControl = false;
  public mbInsert: boolean = false;

  public mskeyValue: string = ""; //FieldExtender
  @ViewChild('FieldExtender') FieldExtender: any; //FieldExtender
  public msCustomDateFormat: string = "";
  public msURL: string = "";
  public mArrGridColumns: any = ComparePriorPresentConstants.gArrLineGridColumns.map(ele => Object.assign({}, ele));
  public mArrGrid = []; //values
  public mArrGridColumns2: any = ComparePriorPresentConstants.gArrLineGridColumns2.map(ele => Object.assign({}, ele));
  public mArrGrid2 = []; //values

  public msRecUserId: string = '';
  public mbdeleteDialogView: boolean = false;
  public mbdeleteLineItem: boolean = false;
  public miRowNumberGrid1: number = 0;
  public miGrid1DeleteLineIndex: number = 0;
  public mbDescriptionDialog: boolean = false;
  public mArrCustomLookutempData: any = [];
  // public miSelectedRowIndex: number = 0;

  public marrDrillDownList: any = [];
  public isRender: boolean = false;
  public mbIsChangeData: boolean = false;
  public mbHyerLinkData: any = null;

  public mbIsNotesModal: boolean = false;
  public msNotesName: string = "";
  public msNotesValue: string = "";
  public miDocType: number = 0;
  public mLookupDesc: any;
  public msFormulaDescription: string = "";
  /* MultiSelectionLookup property*/
  public mbMutiSelectionLookup: boolean = false;

  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
  public mobjUserData: any = {};
  public msHelpFileName: string = '../../../../../../assets/help files/BOM Compare Prior With Present.pdf';

  public mbIsEsignature: boolean = false;
  public mbIsEsignADTPermision: boolean = false;
  public mbIsEsignAuditDialog: boolean = false;
  public mbSignatureModal: boolean = false;
  public mobjSignatureData: any = {};
  public mbCommonAlertDialog: boolean = false;
  public mobjCommonAlertData: any = {};

  public msSystemWTVOL: string = '';
  public msSystemWTVOLValue: any = '';
  public miQtyPrecision: number = 2;
  public miQtyPrecisionFormat: string = 'n' + this.miQtyPrecision;

  public msCurrentGridName = 'dtCurrent';
  public msGridName = 'dtPrior';

  constructor(private location: Location,
    private objService: ViewPriorVersionService,
    private localStorageService: LocalStorageService,
    private activeRoute: ActivatedRoute,
    private objToastService: ToastMessageService,
    private appUtility: AppUtility,
    private objLookupService: LookupService,
    public router: Router,
    private dateFormatService: DateFormatService,
    private userFieldService: UserFieldService,
  ) {
    // this.appUtility.bmgfAddCustomLineToModel(this.mArrGridColumns, this.msScreenName, 'DTLineItem').then(res => { });
    this.bmlfGetDrillDownList();
    this.bmlfGetRoundingDefaults();
  }

  ngOnInit() {
    this.appUtility.getBMEScreePermission(this.msScreenName);
    this.mobjUserData = this.localStorageService.getUserSession();
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.msRecUserId = this.localStorageService.getUserSession().UserId;

    setTimeout(function () { document.getElementById('txtFormulaID').focus(); }, 1000);
    //e-Sgnature -s
    this.mobjSignatureData = {
      scrrenName: this.msScreenName,
      reasonMinLength: 10,
      reqSignAuth: 2,
      date: moment().format(this.msCustomDateFormat.toUpperCase()),
      userName: this.mobjUserData.UserId
    };
    this.bmlfCheckEsignApplicableOnForm();

    this.SetGridColumns(this.msCurrentGridName, this.mArrGridColumns);
    this.SetGridColumns(this.msGridName, this.mArrGridColumns2);
  }
  bmlfOnGlobalFocus(field, value) {
    if (typeof value === 'object' && value.constructor === Object) {
      let valueCopy = Object.assign({}, value);
      this.msLookupField = valueCopy[field];
    } else {
      this.msLookupField = value;
    }
  }
  bmlfOpenLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, pnumRowIdx = null, MutiSelectionLookup = false) {

    // if (psEvent == 'blur' && this.msLookupField == psValue) {
    //   this.msLookupField = '';
    //   return false;
    // }

    if (psEvent == 'blur' && (psValue == undefined || psValue.trim() == '' || this.msLookupField == psValue)) {

      this.msLookupField = '';
      if (psValue == undefined || psValue.trim() == '') {
        this.mobjLookupDataModel[psField] = "";
        if (psLookUpKey == "BOMViewPriorVersionsList") {
          this.mObjModel = Object.assign({}, this.mObjModelBlank);
          this.mObjPriorModel = Object.assign({}, this.mObjPriorModelBlank);
          this.mArrGrid = [];
          this.mArrGrid2 = [];
        }
      }
      return false
    }
    this.mbIsLoading = true;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    let psReqData: any;
    this.mbMutiSelectionLookup = false;

    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      return false;
    }

    if (psLookUpKey == 'BOMViewPriorVersionsList') {
      psReqData = { "ScreenName": psScreenName, "LookUpKey": psLookUpKey, "Filter1": "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
    }
    this.objLookupService.getLookup(psReqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res == null) {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
        return false;
      }
      let obj1: any = {};
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        res.LookupData.forEach(ele => {
          if (ele.RecDate != null || ele.RecDate != undefined) {
            ele.RecDate = moment(ele.RecDate).format(this.msCustomDateFormat.toUpperCase());
          }
          if (ele.RevisionDate != null || ele.RevisionDate != undefined) {
            ele.RevisionDate = moment(ele.RevisionDate).format(this.msCustomDateFormat.toUpperCase());
          }
        });
        this.marrLookupGridData = res.LookupData;
        this.mnumLineItemRowIndex = pnumRowIdx;
        this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;
        if (psLookUpKey == 'BOMViewPriorVersionsList') {
          this.mobjLookupKeyData.title = 'Prior Versions';
        }

        if (psValue) {
          this.mobjLookupKeyData.searchValue = psValue.toString();
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }
        if (psEvent == 'blur' && psValue) {
          let parrFilterdArr: any = [];
          let pobjFirstInput: any = '';
          pobjFirstInput = Object.keys(this.marrLookupGridData[0])[2];
          parrFilterdArr = this.marrLookupGridData.filter(obj => {
            if (obj[pobjFirstInput].toUpperCase() == psValue.toUpperCase()) {
              return obj;
            }
          });

          if (parrFilterdArr.length == 1) {
            this.mobjLookupKeyData.value = parrFilterdArr[0];
            this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
            this.bmlfonSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else {
            this.mbIsLookupModal = true;
          }
        } else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }
      } else {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
      }
    }, err => {
      console.log(err);
      this.mbIsLoading = false;
    });
  }

  bmlfOpenErrorBlock(psMsg): void {
    this.objToastService.notifyWarning(this.appUtility.bmgfonTranslate(psMsg));
  }

  bmlfonSaveLookupField(pobjEvent) {
    this.mbIsLookupModal = false;
    if (pobjEvent.field == 'AssemblyItemKey') {
      this.mObjModel.AssemblyItemKey = pobjEvent.value["Assembly_Item_Key"];
      this.mObjModel.BOMRevision = pobjEvent.value["Bomrevision"];
      this.mObjModel.AssemblyLocation = pobjEvent.value["Assembly_Location"];
      this.bmlfGetDataOnLoad();
    }
  }

  // Set Lookup Value Column
  bmlfGetInputColumn(psfield) {
    if (psfield == 'AssemblyItemKey') {
      return 'Assembly_Item_Key';
    }
  }
  //Return Model Field
  bmlfGetModelField(psfield, eleId) {
    if (psfield == 'AssemblyItemKey') {
      return "AssemblyItemKey";
    }
  }

  bmlfonCloseLookupModal(pobjData) {
    this.mbIsLookupModal = pobjData.isLookupModal;
    if (pobjData.eleId != null) {
      let psInputKey = this.bmlfGetInputColumn(pobjData.field);
      let parrFilterData: any = [];
      if (psInputKey && pobjData.searchValue != null) {
        parrFilterData = pobjData.gridData.filter(obj => obj[psInputKey].toUpperCase() == pobjData.searchValue.toUpperCase());
      }
      if (parrFilterData.length == 0 || pobjData.searchValue == null) {
        let psModelField = this.bmlfGetModelField(pobjData.field, pobjData.eleId);
        this.mObjModel = Object.assign({}, this.mObjModelBlank);
        this.mObjPriorModel = Object.assign({}, this.mObjPriorModelBlank);
        this.mArrGrid = [];
        this.mArrGrid2 = [];
      }
      setTimeout(function () { document.getElementById(pobjData.eleId).focus(); }, 1000);
    }
  }

  bmlfonClose() {
    this.router.navigate([this.msRoutePath]);
  }

  bmlfGetDataOnLoad() {

    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          Assembly_Item_Key: this.mObjModel.AssemblyItemKey,
          Assembly_Location: this.mObjModel.AssemblyLocation,
          BOMRevision: this.mObjModel.BOMRevision,
          Print: true
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": "", "pdfpath": ""
        }]
      })
    };

    this.objService.bmlfGetBOMCompareVersion(reqData).subscribe(res => {
      if (res.HeaderData.length > 0) {
        this.mbCustomControl = true;
        this.mObjModel = res.CurrHeader[0];
        this.mArrGrid = res.CurrDetails;

        this.mObjPriorModel = res.PriorHeader[0];
        this.mArrGrid2 = res.PriorDetails;

        if (this.mArrGrid.length > 0) {
          let piIndex: number = 1;
          this.mArrGrid.forEach(ele => {
            ele.SeqNo = piIndex;
            ele['Entry Date'] = moment(ele['Entry Date']).format(this.msCustomDateFormat.toUpperCase());
            ++piIndex;
          });
        };
        if (this.mArrGrid2.length > 0) {
          let piIndex2: number = 1;
          this.mArrGrid2.forEach(ele => {
            ele.SeqNo = piIndex2;
            ele['Entry Date'] = moment(ele['Entry Date']).format(this.msCustomDateFormat.toUpperCase());
            ++piIndex2;
          });
        };
        this.bmlfGetLookupDes();
        this.bmlfCheckEsignApplicableOnForm();
      }
    });
  }

  isEmpty(value) {
    return (typeof value === "undefined" || value === null || value === '');
  }

  bmlfRedirectFunctionality() {
    if (!CustomValidator.bmgfNullCheckValidator(this.mbHyerLinkData.data.User1)) {
      let queryParamsObj = { queryParams: {} };
      queryParamsObj.queryParams[this.mbHyerLinkData.data.User2] = this.mbHyerLinkData.msField1Value;
      if (!CustomValidator.bmgfNullCheckValidator(this.mbHyerLinkData.data.KeyValue2)) {
        queryParamsObj.queryParams[this.mbHyerLinkData.data.User3] = this.mbHyerLinkData.msField2Value;
      }
      this.router.navigate([this.mbHyerLinkData.data.User1], queryParamsObj);
    }
  }

  bmlfOpenNotesModal(psName: string, psValue: string) {

    if (psValue == " ") { psValue = ""; };
    if (!CustomValidator.bmgfNullCheckValidator(psValue)) {
      this.mbIsNotesModal = true;
      this.msNotesName = psName;
      this.msNotesValue = psValue;
    } else {
      //Key cannot be empty 
      this.objToastService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.NOTES'));
    }
  }

  bmlfCloseNotesModal(pobjEvent: any): void {
    this.mbIsNotesModal = pobjEvent.option;
  }

  bmlfDescriptionModal(event) {
    if (event == 'open') {
      this.mbDescriptionDialog = true;
    } else if (event == 'close') {
      this.mbDescriptionDialog = false;
    }
  }

  bmlfGetDrillDownList() {
    let pobjReqData = {
      ScreenName: 'ApplyPaymentComponent',
      LookUpKey: 'DrillDwnList',
      Filter1: this.msScreenName
    }
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        this.marrDrillDownList = res.LookupData;
        this.isRender = true;
      }

    });
  }

  bmgfDrillDownEvent(pobjData: any) {
    this.mbHyerLinkData = pobjData;
  }

  bmlfViewHelpDoc() {
    window.open(this.msHelpFileName, '_blank');
  }

  //Open Task MODAL 
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.appUtility.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.appUtility.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec: any = {};
      pobjFormRec.formName = this.msScreenName;
      pobjFormRec.primeKey = this.mObjModel.FormulaID;
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL 
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }

  // check e signature
  bmlfCheckEsignApplicableOnForm() {
    let pobjReqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          MenuScreenName: this.msScreenName,
        }],
        Response: [{
          ResponseText: '',
          ErrorCode: ''
        }]
      })
    };
    this.userFieldService.bmlfCheckEsignApplicable(pobjReqData).subscribe(res => {
      if (res == null) {
        return false;
      }
      if (res.Response[0].ErrorCode == '') {
        this.mbIsEsignature = res.ESignData[0].Permision;
        this.mbIsEsignADTPermision = res.ESignData[0].ADTPermision;
        this.mobjSignatureData.reasonMinLength = res.ESignData[0].ReasonLength;
        this.mobjSignatureData.reqSignAuth = res.ESignData[0].NoOfReqSign;
      }
    }, err => {
      console.log(err);
    });
  }

  // open e signature modal
  bmlfopenESignatureModal() {
    this.mbSignatureModal = true;
    this.mobjSignatureData.isShow = true;
    this.mobjSignatureData.primaryKey = this.mObjModel.FormulaID;
  }

  // check e signature
  bmlfChkESignature(pobjData) {
    this.mbSignatureModal = pobjData.isShow;
    if (pobjData.event == 'Save') {
      this.mbIsEsignature = false;
      // this.mobjm.ESG_REASON = pobjData.changeReason;
      // this.mObjDetailModel.ESG_APPROVER = pobjData.eSignApprover;
      this.mbChange = true;
      //this.bmlfonSubmitData();
    } else if (pobjData.event == 'Cancel') {
      this.mobjCommonAlertData = {}
      this.mbCommonAlertDialog = true;
      this.mobjCommonAlertData.message = this.appUtility.bmgfonTranslate('ESIGNREASONMSG');
      this.mobjCommonAlertData.event = 'close';
      this.mbChange = true;
    }
  }

  // ESIGN Audit alert event 
  bmlfEsignAuditAlertEvent(event) {
    if (event == 'No') {

    } else {
      this.mbIsEsignAuditDialog = false;
    }
  }
  // CommonAlertDialog
  bmlfmbCommonAlertDialog(event) {
    this.mbCommonAlertDialog = false;
  }

  bmlfGetRoundingDefaults() {
    let parrRoundingDefaults = JSON.parse(localStorage.getItem("RoundingDefaults"));
    parrRoundingDefaults.forEach((ele) => {
      if (ele.Default_Key == 'Formula/Bom') {
        this.miQtyPrecision = ele.Default_Value;
        this.miQtyPrecisionFormat = 'n' + this.miQtyPrecision;
      }
    });

    let parrUMCONFIG = JSON.parse(localStorage.getItem("UMCONFIG"));
    this.msSystemWTVOL = parrUMCONFIG[0].WtUom + '/' + parrUMCONFIG[0].VolUOM;
  }


  bmlfGetLookupDes() {
    let parrSetKeyData = [
      { KeyCode: 'ITEMKEY', KeyValue1: this.mObjModel.AssemblyItemKey, KeyValue2: '', KeyDescription: '' },
      { KeyCode: 'LOCATION', KeyValue1: this.mObjModel.AssemblyLocation, KeyValue2: '', KeyDescription: '' },
    ];

    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: parrSetKeyData,
        ScreenName: [{ ScreenName: this.msScreenName }],
        Response: [{ ResponseText: '', ErrorCode: '' }]
      })
    };
    this.userFieldService.bmlfFetchKeyDescription(reqData).subscribe(res => {
      this.mLookupDesc = res.LookupData;
      this.mLookupDesc.forEach(element => {
        if (element.KeyCode == 'ITEMKEY') {
          this.mObjModel.AssemblyItemKeyDescription = element.KeyDescription;
        }
        if (element.KeyCode == 'LOCATION') {
          this.mObjModel.AssemblyLocationDescription = element.KeyDescription;
        }
      });
    });
  }

  SetGridColumns(GridName, parrGridCol) {
    this.appUtility.GetJsonSetting(this.msScreenName, this.mobjUserData.UserId, GridName, parrGridCol).then((pArrGridChangedCol: any) => {
      if (pArrGridChangedCol != null && pArrGridChangedCol != undefined) {
        parrGridCol = pArrGridChangedCol
      }
    }, err => {
      console.log(err);
    });
  }
}
