    /**------------------Resequence Functionality ------------------- */
    bmlfChangeSeqNo(index) {
        // this.marrItemDataList.forEach(x=> {
        for (let x: any = 0; x < this.marrItemDataList.length; x++) {
            if (this.marrItemDataList[x].LineNo == this.marrItemDataList[index].SeqNo) {
                this.marrItemDataList[x].SortKey = this.marrItemDataList[x].SortKey + 1;
                break;
            }
        }
        // })
        // if (this.marrItemDataList[index].SeqNo != this.marrItemDataList[index].LineNo) {
        //     this.marrItemDataList[index].SortKey = this.marrItemDataList[index].SortKey + 1;
        // } else {
        //     this.marrItemDataList[index].SortKey = 1;
        // }
    }
    bmlfResequenceData() {
        if (this.marrItemDataList.length > 0) {
            this.mbIsLoading = true;
            let parrDataList = [];
            this.marrItemDataList.map(x => {
                let ele = Object.assign({}, x);
                ele = this.bmlfSetDataBeforeSave(ele)
                parrDataList.push(ele);
            });
            let reqData: any = {
                JSON: JSON.stringify({
                    BOMDetails: parrDataList,
                    Response: [{
                        "ResponseText": "", "ErrorCode": ""
                    }]
                })
            };
            this.bomEntryService.bmlfResequenceBOMRecord(reqData).subscribe(res => {
                this.mbIsLoading = false;
                if (res == null) {
                    this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
                    return false;
                }
                if (res.Response[0].ErrorCode == "") {
                    {
                        this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("BOMENTRY.MSG.RESEQUENCESUCESS"));
                        let parrItemDataList = res.BOMDetails;
                        this.marrItemDataList = [];
                        parrItemDataList.forEach(element => {
                            let parrFilterUnitData = this.marrItemUnitList.filter(x => {
                                if (x.Itemkey == element.ItemKey) {
                                    return x;
                                }
                            });
                            if (parrFilterUnitData.length > 0) {
                                element.unitList = parrFilterUnitData;
                            }
                            element.EntryDate = element.EntryDate == null ? null : new Date(element.EntryDate);
                            //  element.RecDate = element.RecDate == null ? null : new Date(element.RecDate);
                            element.DispMatCost = this.utilService.bmgfSetPrecision(element.DispMatCost, this.miCostPrecision);
                            element.DispExMatCost = this.utilService.bmgfSetPrecision(element.DispExMatCost, this.miAmountPrecision);
                            element.InvConvFact = element.InvConvFact.toString();
                            element.DispReqQty = Number(element.DispReqQty);
                            element.OldUnit = element.DispUOM;
                            this.marrItemDataList.push(Object.assign({}, element));
                        });
                    }
                } else {
                    this.toastMessageService.notifyError(res.Response[0].ResponseText);
                }
            })
        } else {
            this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('BOMENTRY.MSG.SUBASSEMBLYLINE'));
        }

    }
    /**---------------Release Functionality ----------*/
    bmlfReleaseData() {
        this.mbIsLoading = true;
        let parrHeaderData = [];
        parrHeaderData = [{ AssemblyItemKey: this.mobjBOMEntryModel.AssemblyItemKey, AssemblyLocation: this.mobjBOMEntryModel.AssemblyLocation, RevisionNo: this.mobjBOMEntryModel.RevisionNo }]
        let reqData: any = {
            JSON: JSON.stringify({
                HeaderData: parrHeaderData,
                Response: [{
                    "ResponseText": "", "ErrorCode": ""
                }]
            })
        };
        this.bomEntryService.bmlfReleaseBOMRecord(reqData).subscribe(res => {
            this.mbIsLoading = false;
            if (res == null) {
                this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
                return false;
            }
            if (res.Response[0].ErrorCode == "") {
                {
                    if (res.Response[0].ResponseText.includes('exist') || res.Response[0].ResponseText.includes('Exists') || res.Response[0].ResponseText.includes('NEW')) {
                        this.toastMessageService.notifyError(res.Response[0].ResponseText);
                    } else {
                        this.toastMessageService.notifySuccess(res.Response[0].ResponseText);
                        this.mobjBOMEntryModel.Status = "REL";
                        this.bmlfGetBomEntryDetail();
                    }
                }
            } else {
                this.toastMessageService.notifyError(res.Response[0].ResponseText);
            }
        })
    }
