    /**--------START: Sales Order Detail-------------- */
    bmlfOpenSalesOrderDetail() {
        
        if (CustomValidator.bmgfNullCheckValidator(this.mobjBOMEntryModel.AssemblyItemKey)) {
            this.bmlfOpenErrorBlock("BOMENTRY.MSG.TOPASSEMBLYREQ");
            return;
        }
        let psAssemblyType: any = ''
        if (this.mobjBOMEntryModel.AssemblyType == 'Assembly') {
            psAssemblyType = "I";
        } else {
            psAssemblyType = "F";
        }
        this.msSODetailRadioButton = psAssemblyType;
        this.modalservice.open("salesOrderDetailModal");
        this.bmlfChangeSODetail(psAssemblyType);
    }
    bmlfChangeSODetail(type) {
        this.mbIsLoading = true;
        
        this.marrSalesOrderDetail = [];
        let psReqData: any;
        if (type == 'F') {
            let psFormulaId: any = '░';
            if (this.mobjBOMEntryModel.FormulaID != '') {
                psFormulaId = this.mobjBOMEntryModel.FormulaID;
            }
            psReqData = { "ScreenName": this.msScreenName, "LookUpKey": "SODetailsFormulaWise", "Filter1": psFormulaId, "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
        } else {
            psReqData = { "ScreenName": this.msScreenName, "LookUpKey": "SODetailsItemLocWise", "Filter1": this.mobjBOMEntryModel.AssemblyItemKey, "Filter2": this.mobjBOMEntryModel.AssemblyLocation, "Filter3": "", "Filter4": "", "Filter5": "" };
        }
        this.bomEntryService.bmlfGETGeneralSqlData(psReqData).subscribe(res => {
            this.mbIsLoading = false;
            if (res == null) {
                return false;
            }
            if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
                let parrGridData = res.LookupData;
                parrGridData.forEach(ele => {
                    ele.ReqDate = new Date(ele.ReqDate);
                })
                this.marrSalesOrderDetail = parrGridData;
            }
        });
    }

    bmlfCloseInnerModalSODetail(): void {
        this.modalservice.close("salesOrderDetailModal");
    }
