    // Description expanded Modal
    bmlfDescriptionModal(event, index, field) {
        if (event == 'open') {
            this.miRowIndex = index;
            this.msCommentField = field;
            if (field == "Comments") {
                this.msCommentMaxlength = 60;
            } else {
                this.msCommentMaxlength = 1000;
            }
            if (index >= 0) {
                // this.CommentDescription = this.marrItemDataList[this.miRowIndex].Comments;
            } else {
                this.CommentDescription = this.mobjBOMEntryModel[field];
            }
            this.mbCommentDialog = true;
        } else if (event == 'close') {
            if (this.miRowIndex >= 0) {
                //this.marrItemDataList[this.miRowIndex].Comments = this.CommentDescription;
            } else {
                if (this.msCommentField == "Comments") {
                    this.mobjBOMEntryModel.Comments = this.CommentDescription;
                }
            }
            this.miRowIndex = -1;
            this.CommentDescription = "";
            this.mbCommentDialog = false;
        }
    }
