    bmlfOnProcess() {
        if (this.mobjBOMEntryModel.AssemblyItemKey != '') {
            this.mbIsLoading = true;
            //this.mobjBOMEntryModel.ReportName ="Inventory Analysis Report";
            let pDefaultReportType = localStorage.getItem('reportType');
            let reqData: any = {
                JSON: JSON.stringify({
                    DistReport: [{
                        'ReportName': 'BOM Report',
                        'TopAssemblyItemFrom': this.mobjBOMEntryModel.AssemblyItemKey,
                        'TopAssemblyItemTo': this.mobjBOMEntryModel.AssemblyItemKey,
                        'LocationFrom': this.mobjBOMEntryModel.AssemblyLocation,
                        'LocationTo': this.mobjBOMEntryModel.AssemblyLocation,
                        'FormulaFrom': this.mobjBOMEntryModel.FormulaID,
                        'FormulaTo': this.mobjBOMEntryModel.FormulaID,
                        'AssemblyType': this.mobjBOMEntryModel.AssemblyType,
                        'Status': this.mobjBOMEntryModel.Status,
                        'ListingFlag': 'false',
                        'ListingVariable': 'false'
                    }],

                    Response: [{ 'ResponseText': '', 'ErrorCode': '', 'Pdfpath': '' }]
                })
            };
            this.service.GetReportData(reqData).subscribe(res => {
                
                this.mbIsLoading = false;
                if (res != null) {
                    if (res.Response[0].ErrorCode == "") {
                        let pPDFSrc = res.Response[0].Pdfpath;
                        if (pDefaultReportType == 'Print') {
                            this.utilService.bmgfPrintPDF(pPDFSrc);
                        } else if (pDefaultReportType == 'NewTab') {
                            this.utilService.bmgfOpenPDFNewTab(pPDFSrc);
                        } else {
                            this.utilService.bmgfDownloadFile(pPDFSrc, this.msScreenName, pDefaultReportType);
                        }

                    }
                    else {
                        if (res.Response[0].ErrorCode == '2001') {
                            this.toastMessageService.notifyError(res.Response[0].ResponseText);
                        } else if (res.Response[0].ErrorCode != '' && res.Response[0].ErrorCode != '2001') {
                            this.bmlfOpenErrorBlock(res.Response[0].ErrorCode)
                        }
                        else {
                            this.bmlfOpenErrorBlock("No Data Found")
                        }
                        this.mbIsLoading = false;
                    }
                }
            });
        }
    }

