  //Load
  ngOnInit() {

    // Begin Customization in Grid
    this.objUtils.bmgfAddCustomLineToModel(this.mArrContainerBomGrid1Col, this.msscreenName, 'DTLineitem').then(res => { });
    // End Customization in Grid
    this.msKey = this.activeRoute.snapshot.queryParamMap.get("ContainerKey") == null ? '' : this.activeRoute.snapshot.queryParamMap.get("ContainerKey");
    // this.msKey1 = this.activeRoute.snapshot.queryParamMap.get("Location") == null ? '' : this.activeRoute.snapshot.queryParamMap.get("Location");
    this.msCopyRecord = this.activeRoute.snapshot.queryParamMap.get("CopyRecord");
    /**--- Get default formats from localStorage--- */
    let parrDefaultFormat = JSON.parse(localStorage.getItem("defaultFormats"));
    parrDefaultFormat.forEach(ele => {
      if (ele.Default_Key == "CURR_DECIMAL") {
        this.miAmountPrecision = Number(ele.Default_Value);
        this.miAmountPrecisionFormat = 'n' + this.miAmountPrecision;
      }
    });
    let parrRoundingDefaults = JSON.parse(localStorage.getItem("RoundingDefaults"));
    this.miQtyPrecision = Number(parrRoundingDefaults[0].Default_Value);
    this.miQtyPrecisionFormat = 'n' + this.miQtyPrecision;
    /**--- Get default date format from service--- */
    this.bmlfGetQtyPrecision();
    this.mobjUserData = this.localStorageService.getUserSession();
    this.objUtils.getBMEScreePermission(this.msscreenName);
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.mskeyValue = this.msKey;
    this.msRecUserID = this.localStorageService.getUserSession().UserId;

    if (this.msKey != "") {
      this.bmlfGetDataOnLoad(this.msKey, this.msKey1);
    }
    else {
      this.bmlfonClearControl();
      setTimeout(function () { document.getElementById('txtContainerKey').focus(); }, 500);
      this.mbCustomControl = true;

    }


    //Begin 18/Jul/2020
    this.mobjUserData = this.localStorageService.getUserSession();
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();

    this.mobjSignatureData = {
      scrrenName: this.msscreenName,
      reasonMinLength: 10,
      reqSignAuth: 2,
      date: moment().format(this.msCustomDateFormat.toUpperCase()),
      userName: this.mobjUserData.UserId
    };
    this.bmlfCheckEsignApplicableOnForm();
    // End 18/Jul/2020
    this.SetGridColumns('DTLineitem', this.mArrContainerBomGrid1Col);
  }

