  bmlfonDialogAction(action) {

    if (action == 'No') {
      this.mbShowDialog = false;
      //this.mbChange = false;
      if (this.msCallFor == "FormClose") {
        this.router.navigate(['/main/bom/containerbomentry']);
      }
      else if (this.msCallFor == "Add") {
        this.bmlfonClearControl();
      }
      else if (this.msCallFor == "Copy") {
        this.bmlfonSetFormMode("I", "Copy");
      }
      else if (this.msCallFor == "Search") {
        this.bmlfOpenLookupModal('Container BOM Entry', 'ContainerBOMList', 'ContainerKey', 'click', '', 'custom', 'txtContainerKey');
      }
      else {
        return;
      }

    } //Yes
    else if (action == 'Yes' && this.msCallFor == "FormClose") {
      this.bmlfonSubmitData();
    }
    else if (action == 'Yes' && this.msCallFor == "Add") {
      this.bmlfonSubmitData();
    }
    else if (action == 'Yes' && this.msCallFor == "Copy") {
      this.bmlfonSubmitData();
    }
    else if (action == 'Yes' && this.msCallFor == "Search") {
      this.bmlfonSubmitData();
      this.mbShowDialog = false;

      // if (this.mbResult == false) {
      //   return;
      // }
      // setTimeout(() => {      }, 1000);
      //this.bmlfOpenLookupModal('Container BOM Entry', 'ContainerBOMList', 'ContainerKey', 'click', '', 'custom', 'txtContainerKey');
    }
    else if (action == 'Yes') {
      // call update function
      this.mbChange = false;
      this.mbShowDialog = false;
      this.msFormMode = "U";
      this.mbDisabledPrimaryKey = true;
    }
    else {
      this.mbShowDialog = false;
    }
    setTimeout(function () { document.getElementById('txtContainerKey').focus(); }, 10);
  }

  bmlfGetDataOnLoad(psKey, psKey1) {


    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          ContainerKey: psKey
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.objService.bmlfonGet(reqData).subscribe(res => {
      if (res.HeaderData.length > 0) {
        this.mbCustomControl = true;
        this.msFormMode = "U";
        this.mbDisabledPrimaryKey = true;
        this.mObjModel = res.HeaderData[0];

        //Begin Format date/time columns in customization grid field to remove "T"
        this.objUtils.bmlfTransformCustomFieldsGet(res.DetailData)
        //end Format date/time columns in customization grid field

        this.mArrContainerBomGrid1 = res.DetailData;
        //this.mArrSalesKitGrid2 =  res.SubDetails;
        this.marrItemUnitList = res.ContainerUOM;


        this.mArrContainerBomGrid1.forEach(element => {
          element.MaterialCost = parseFloat(element.MaterialCost)
          element.ExtMaterialCost = parseFloat(element.ExtMaterialCost)
          element.InvConvFact = parseFloat(element.InvConvFact)
          let parrFilterUnitData = this.marrItemUnitList.filter(x => {
            if (x.Itemkey == element.ItemKey) {
              return x;
            }
          });
          if (parrFilterUnitData.length > 0) {
            element.unitList = parrFilterUnitData;
          }
          element.OldUnit = element.DispUOM;
        });
        this.mbCustomControl = true;
        //this.bmlfSetToggleUnit()
        //this.bmlfGetCurrency(this.mObjModel.CUSTKEY.trim());
        //this.bmlfGetDescription(this.mObjModel);

      }
      else {
        this.msFormMode = "I";
        this.mbDisabledPrimaryKey = false;
      }
      if (this.msCopyRecord == "Y") {
        this.bmlfonSetFormMode("I", "Copy");
        // this.msFormMode = "I";
        // this.mObjModelContainerKey = ""; // clear the primary key
        // this.mbDisabledPrimaryKey = false;
        // this.mbChange = true;
        // setTimeout(function(){document.getElementById('txtContainerKey').focus();},10);
      }
    })
    this.bmlfCheckEsignApplicableOnForm();   // 18/jul/2020
    this.bmlfonURL(this.msKey)
  }

