  bmlfonDeleteDialogAction(action) {

    if (action == 'No') {
      this.mbShowDeleteDialog = false;
    } else if (action == 'Yes') {
      if (this.mbInsert && this.mbChange) {
        this.mbShowDeleteDialog = false;
        let deleteKey = this.msscreenName + " — " + this.mObjModel.ContainerKey;
        this.FieldExtender.bmgfDeleteData(deleteKey)
        this.bmlfonClearControl();
        //this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('PRICEMATRIX.MSGDELETE'));
        //this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('COMMON.MSG.NODATADELETE'));
        return false;
      }
      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: [{
            ContainerKey: this.mObjModel.ContainerKey
          }],
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.objService.bmlfonDelete(reqData).subscribe(res => {
        if (res == null) {
          //this.objToastService.notifyError('Server Error');
          this.objToastService.notifyError(this.objUtils.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          if (res.Response[0].ResponseText.search("Reference") == -1) {
            this.mbShowDeleteDialog = false;
            this.objToastService.notifySuccess((this.objUtils.bmgfonTranslate('COMMON.MSG.DELETE')));
            this.bmlfonClearControl();
          }
          else {
            this.objToastService.notifyError(this.objUtils.bmgfonTranslate('COMMON.MSG.EXIST_REF'));
            this.mbShowDeleteDialog = false;
            return;
          }
          // {
          //   if (res.Response[0].ResponseText.search('Deleted') != -1) {
          // this.mbShowDeleteDialog = false;
          // this.objToastService.notifySuccess((this.objUtils.bmgfonTranslate('COMMON.MSG.DELETE')));
          // this.bmlfonClearControl();
          //   } else {
          //     this.objToastService.notifyError(res.Response[0].ResponseText)
          //     this.mbShowDeleteDialog = false;
          //   };

          // }
        } else {
          this.objToastService.notifyError(res.Response[0].ResponseText)
          this.mbShowDeleteDialog = false;
        }
      })
    }
  }

