  bmlfonValidateFormDataSubmit() {
    if (this.mObjModel.ContainerKey.trim() == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('CONTAINERBOMENTRY.ENTERITEMKEY')));
      return false;
    }

    if (this.mObjModel.FillUOM.trim() == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('CONTAINERBOMENTRY.ENTERFILLUOM')));
      return false;
    }

    if (this.mArrContainerBomGrid1.length == 0) {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('CONTAINERBOMENTRY.GRID1LENGTH')));
      return false;
    }


    // //Check Duplicate row
    let pbBlank = false;
    let psKey: string = '';

    this.mArrContainerBomGrid1.forEach(ele => {
      if (ele.ItemKey == "" && ele.LineName != 'Text') {
        psKey = ele.ItemKey;
        pbBlank = true;//1
      };
      // let parrNewLotExist = this.mArrContainerBomGrid1.filter(x => x.Kit_Item == ele.Kit_Item && x.Kit_Location == ele.Kit_Location);
      // if (parrNewLotExist.length > 1) {
      //   psDuplicate = ele.Kit_Item;
      //   pbDuplicate = true; //2
      // }

    })

    //1
    if (pbBlank) {
      this.objToastService.notifyError(psKey + ' ' + (this.objUtils.bmgfonTranslate('CONTAINERBOMENTRY.KITITEMBLANK')));
      return false;
    };


    //Validate Field Extender -- Start
    let resultString = this.FieldExtender.bmgfValidationCheck();
    if (resultString.value == false) {
      this.objToastService.notifyWarning(resultString.msg);
      return false;
    }
    //All validation success


    return true;
  }

