  bmlfResequenceDataSAVE() {

    return new Promise((resolve, reject) => {
      this.mbIsLoading = true;
      let parrDataList = [];
      this.mArrContainerBomGrid1.map(x => {
        let ele = Object.assign({}, x);
        ele = this.bmlfSetDataBeforeSave(ele)
        parrDataList.push(ele);
      });

      let reqData: any = {
        JSON: JSON.stringify({
          DetailData: parrDataList,
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.objService.BMGFContainerBOMResequence(reqData).subscribe(res => {
        this.mbIsLoading = false;
        if (res == null) {
          this.objToastService.notifyError(this.objUtils.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
          reject();
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          {
            // if(this.msCallFor != "Save"){
            //   this.objToastService.notifySuccess(this.objUtils.bmgfonTranslate("BOMENTRY.MSG.RESEQUENCESUCESS"));
            // }
            this.mArrContainerBomGrid1 = res.ContBOMLines;


            let parrItemDataList = res.ContBOMLines;
            this.mArrContainerBomGrid1 = [];
            parrItemDataList.forEach(element => {
              let parrFilterUnitData = this.marrItemUnitList.filter(x => {
                if (x.Itemkey == element.ItemKey) {
                  return x;
                }
              });
              if (parrFilterUnitData.length > 0) {
                element.unitList = parrFilterUnitData;
              }
              //element.EntryDate = element.EntryDate == null ? null : new Date(element.EntryDate);
              //element.RecDate = element.RecDate == null ? null : new Date(element.RecDate);
              // element.MaterialCost = this.objUtils.bmgfSetPrecision(element.MaterialCost, this.miCostPrecision);
              // element.ExtMaterialCost = this.objUtils.bmgfSetPrecision(element.ExtMaterialCost, this.miAmountPrecision);
              // element.InvConvFact = element.InvConvFact.toString();
              // element.DispQty = Number(element.DispQty);
              element.OldUnit = element.DispUOM;
              this.mArrContainerBomGrid1.push(Object.assign({}, element));
            });
            this.mbChange = true;
            resolve(true);
            // let parrItemDataList = res.BOMDetails;
            // this.marrItemDataList = [];
            // parrItemDataList.forEach(element => {
            //     let parrFilterUnitData = this.marrItemUnitList.filter(x => {
            //         if (x.Itemkey == element.ItemKey) {
            //             return x;
            //         }
            //     });
            //     if (parrFilterUnitData.length > 0) {
            //         element.unitList = parrFilterUnitData;
            //     }
            //     element.OldUnit = element.DispUOM;
            //     this.marrItemDataList.push(Object.assign({}, element));
            // });

          }
        } else {
          this.objToastService.notifyError(res.Response[0].ResponseText);
          resolve(false);
        }
      })
    })

  }

  /** change event of Item unit  */
  bmlfChangeItemUnitEvent(pnumIndex: number, pobjData: any): void {
    let piOldQty = 0;
    let piMaterialCostQty = 0;

    let parrFilterUnitData = this.marrItemUnitList.filter(x => {
      if (x.ToKey == pobjData.DispUOM && x.Itemkey == pobjData.ItemKey) {
        return x;
      }
    });

    /** --- get unit conversation acc to old unit--- */
    let parrOldData = this.marrItemUnitList.filter(x => {
      if (x.ToKey == pobjData.OldUnit && x.Itemkey == pobjData.ItemKey) {
        return x;
      }
    });
    if (parrOldData.length > 0) {
      /**---- if  unit and old  unit is same */
      if (parrOldData[0].FromKey == pobjData.OldUnit) {
        piOldQty = pobjData.DispQty;
        piMaterialCostQty = pobjData.MaterialCost;
      } else {  // if not same  then reverse unit conversation for achieve initial value
        if (parrOldData[0].Operation == "1") {
          piOldQty = this.objUtils.bmgfSetPrecision((1 / parrOldData[0].convfctr) * Number(pobjData.DispQty), this.miQtyPrecision);
          piMaterialCostQty = Number(pobjData.MaterialCost) > 0 ? this.objUtils.bmgfSetPrecision(parrOldData[0].convfctr * Number(pobjData.MaterialCost), this.miCostPrecision) : this.objUtils.bmgfSetPrecision(0, this.miCostPrecision);

        } else {
          piOldQty = this.objUtils.bmgfSetPrecision(parrOldData[0].convfctr * Number(pobjData.DispQty), this.miQtyPrecision);
          piMaterialCostQty = Number(pobjData.MaterialCost) > 0 ? this.objUtils.bmgfSetPrecision((1 / parrOldData[0].convfctr) * Number(pobjData.MaterialCost), this.miCostPrecision) : this.objUtils.bmgfSetPrecision(0, this.miCostPrecision);
        }
      }
    }
    if (parrFilterUnitData.length > 0) {
      if (parrFilterUnitData[0].Operation == "2") {
        this.mArrContainerBomGrid1[pnumIndex].DispQty = (1 / parrFilterUnitData[0].convfctr) * Number(piOldQty);
        this.mArrContainerBomGrid1[pnumIndex].MaterialCost = this.objUtils.bmgfSetPrecision(parrFilterUnitData[0].convfctr * Number(piMaterialCostQty), this.miCostPrecision);
        this.mArrContainerBomGrid1[pnumIndex].InvConvFact = parrFilterUnitData[0].convfctr.toString();
      } else {
        this.mArrContainerBomGrid1[pnumIndex].DispQty = parrFilterUnitData[0].convfctr * Number(piOldQty);
        this.mArrContainerBomGrid1[pnumIndex].MaterialCost = this.objUtils.bmgfSetPrecision((1 / parrFilterUnitData[0].convfctr) * Number(piMaterialCostQty), this.miCostPrecision);

        let toStr = (1 / parrFilterUnitData[0].convfctr).toFixed(3);
        this.mArrContainerBomGrid1[pnumIndex].InvConvFact = toStr.toString();
      }
      this.mArrContainerBomGrid1[pnumIndex].OldUnit = this.mArrContainerBomGrid1[pnumIndex].DispUOM;
    }
  }

