import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from 'src/app/core/guards/auth.guard';
import { BomComponent} from '././bom.component';

import { ContainerBomEntryListComponent } from './components/containerbomentry/containerbomentry-list/containerbomentry-list.component';
import { ContainerBomEntryManageComponent } from './components/containerbomentry/containerbomentry-manage/containerbomentry-manage.component';
import { CoProductFormulaFinishedGoodListComponent } from './components/coproductformulafinishedgood/coproductformulafinishedgood-list/coproductformulafinishedgood-list.component';
import { CoProductFormulaFinishedGoodManageComponent } from './components/coproductformulafinishedgood/coproductformulafinishedgood-manage/coproductformulafinishedgood-manage.component';
import { BOMEntryListComponent } from './components/bom-entry/bom-entry-list/bom-entry-list.component';
import { BOMEntryManageComponent } from './components/bom-entry/bom-entry-manage/bom-entry-manage.component';
import { BOMPurgePriorVersionsComponent } from './components/bom-purge-prior-versions/bom-purge-prior-versions.component';
import { BomCostRollUpComponent } from './components/bom-cost-roll-up/bom-cost-roll-up.component';
import { BomViewPriorVersionComponent } from './components/bom-view-prior-version/bom-view-prior-version.component';
import { BomComparePriorPresentComponent } from './components/bom-compare-prior-present/bom-compare-prior-present.component';
import { BomExplosionComponent } from './components/bom-explosion/bom-explosion.component';

const routes: Routes = [ 
    {
        path: '',
        component: BomComponent,
        children:
          [
            {
              path: '', redirectTo: 'bom', pathMatch: 'full'
            },        
            {
              path: 'containerbomentry', component: ContainerBomEntryListComponent, canActivate: [AuthGuard]
            },
            {
              path: 'containerbomentry/manage', component: ContainerBomEntryManageComponent, canActivate: [AuthGuard]
            },
            {
              path: 'coproductformulafinishedgood', component: CoProductFormulaFinishedGoodListComponent, canActivate: [AuthGuard]
            },
            {
              path: 'coproductformulafinishedgood/manage', component: CoProductFormulaFinishedGoodManageComponent, canActivate: [AuthGuard]
            } ,
            {
              path: 'bomEntry', component: BOMEntryListComponent, canActivate: [AuthGuard]
            },
            {
              path: 'bomEntry/manage', component: BOMEntryManageComponent, canActivate: [AuthGuard]
            },          
            {
              path: 'bom-purge-prior-versions', component: BOMPurgePriorVersionsComponent, canActivate: [AuthGuard]
            },   
            {
              path: 'bom-cost-roll-up', component: BomCostRollUpComponent, canActivate: [AuthGuard]
            },  
            {
              path: 'bom-view-prior-versions', component: BomViewPriorVersionComponent, canActivate: [AuthGuard]
            }, 
            {
              path: 'bom-revert-prior-version', component: BomViewPriorVersionComponent, canActivate: [AuthGuard]
            },   
            {
              path: 'bom-compare-prior-present', component: BomComparePriorPresentComponent, canActivate: [AuthGuard]
            },   
            {
              path: 'bom-explosion', component: BomExplosionComponent, canActivate: [AuthGuard]
            },
          ]
      }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class BomRoutingModule { }




