  //Close
  bmlfonClose() {
    if (!this.mbChange && !this.FieldExtender.mbChange && !this.customCtrl.mbChange) {
      this.router.navigate(['/main/bom/coproductformulafinishedgood']);
    } else {
      this.mbShowDialog = true;
      this.msCallFor = "FormClose";
    }
  }


  bmlfonDialogAction(action) {
    if (action == 'No') {
      this.mbShowDialog = false;
      //this.mbChange = false;
      if (this.msCallFor == "FormClose") {
        this.router.navigate(['/main/bom/coproductformulafinishedgood']);
      } else if (this.msCallFor == "Add") {
        this.bmlfonClearControl();
      } else if (this.msCallFor == "Copy") {
        this.bmlfonSetFormMode("I", "Copy");
      } else if (this.msCallFor == "Search") {
        this.bmlfOpenLookupModal('CoProduct Formula/FInished Good Template', 'CoProductList', 'HeaderLookup', 'click', '', 'custom', 'txtTemplateKey');
      } else {
        return;
      }
    } else if (action == 'Yes' && this.msCallFor == "FormClose") {
      this.bmlfonSubmitData();
    } else if (action == 'Yes' && this.msCallFor == "Add") {
      this.bmlfonSubmitData();
    } else if (action == 'Yes' && this.msCallFor == "Copy") {
      this.bmlfonSubmitData();
    } else if (action == 'Yes' && this.msCallFor == "Search") {
      this.bmlfonSubmitData();
      this.mbShowDialog = false;
    } else if (action == 'Yes') {
      // call update function
      this.mbChange = false;
      this.mbShowDialog = false;
      this.msFormMode = "U";
      this.mbDisabledPrimaryKey = true;
    } else {
      this.mbShowDialog = false;
    }
    setTimeout(function () { document.getElementById('txtTemplateKey').focus(); }, 10);
  }

  bmlfGetDataOnLoad(psKey, psKey1) {
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          TemplateKey: psKey
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.objService.bmlfonGet(reqData).subscribe(res => {
      if (res.HeaderData.length > 0) {
        this.msFormMode = "U";
        this.mbDisabledPrimaryKey = true;
        this.mObjModel = res.HeaderData[0];

        //Begin Format date/time columns in customization grid field to remove "T"  
        this.objUtils.bmlfTransformCustomFieldsGet(res.DetailData)
        //end Format date/time columns in customization grid field 

        this.mArrCoProductFormulaGrid1 = res.DetailData;
        this.bmlfGetDescription(this.mObjModel);
        this.mbCustomControl = true; // Customization 

      }
      else {
        this.msFormMode = "I";
        this.mbDisabledPrimaryKey = false;
      }
      if (this.msCopyRecord == "Y") {
        this.bmlfonSetFormMode("I", "Copy");
      }
    })
    this.bmlfCheckEsignApplicableOnForm();   // 18/jul/2020
    this.bmlfonURL(this.msKey)
  }

  bmlfonChange() {
    this.mbChange = true;
  }

  bmlfonValidateFormDataSubmit() {
    if (this.mObjModel.TemplateKey.trim() == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('COPRODUCTFORMULA.ENTERITEMKEYTEMPLATE')));
      return false;
    }

    if (this.mObjModel.FormulaID.trim() == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('COPRODUCTFORMULA.ENTERITEMKEY1')));
      return false;
    }

    if (this.mArrCoProductFormulaGrid1.length == 0) {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('COPRODUCTFORMULA.GRID1LENGTH')));
      return false;
    }

    //Check Duplicate row
    let pbBlank = false;
    let psKey: string = '';
    let pbDuplicate: boolean = false;
    let psDuplicate: string = '';
    this.mArrCoProductFormulaGrid1.forEach(ele => {
      let parrNewLotExist = this.mArrCoProductFormulaGrid1.filter(x => x.ItemKey == ele.ItemKey && x.Location == ele.Location && x.Description == ele.Description);
      if (parrNewLotExist.length > 1) {
        psDuplicate = ele.ItemKey;
        pbDuplicate = true; //2
      }
    })

    //1
    if (pbBlank) {
      this.objToastService.notifyError(psKey + ' ' + (this.objUtils.bmgfonTranslate('COPRODUCTFORMULA.DuplicateRow')));
      return false;
    };

    //2
    if (pbDuplicate) {
      this.objToastService.notifyError(psDuplicate + ' ' + (this.objUtils.bmgfonTranslate('COPRODUCTFORMULA.DuplicateRow')));
      return false;
    }

    //Validate Field Extender -- Start
    let resultString = this.FieldExtender.bmgfValidationCheck();
    if (resultString.value == false) {
      this.objToastService.notifyWarning(resultString.msg);
      return false;
    }
    //All validation success
    return true;
  }

  bmlfonValidateFormData() {
    if (this.mObjModel.TemplateKey.trim() == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('COPRODUCTFORMULA.ENTERITEMKEYTEMPLATE')));
      return false;
    }

    if (this.mObjModel.FormulaID.trim() == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('COPRODUCTFORMULA.ENTERITEMKEY1')));
      return false;
    }

    if (this.mArrCoProductFormulaGrid1.length == 0) {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('COPRODUCTFORMULA.GRID1LENGTH')));
      return false;
    }
    //Validate Field Extender -- Start
    let resultString = this.FieldExtender.bmgfValidationCheck();
    if (resultString.value == false) {
      this.objToastService.notifyWarning(resultString.msg);
      return false;
    }
    //All validation success
    return true;
  }


