  bmlfChkBlankLine() {
    let isBlankLine = false;
    this.mArrCoProductFormulaGrid1.some((pobjEle, piRowIdx) => {
      let rowNo = piRowIdx + 1;
      let psMsg = this.objUtils.bmgfonTranslate('COPRODUCTFORMULA.COMPLETEROWENTRY') + ' ' + '(' + this.objUtils.bmgfonTranslate('COMMON.ROWNO') + rowNo + ')';
      if (pobjEle.ItemKey == '' || pobjEle.Location == '') {
        this.objToastService.notifyError(psMsg);
        isBlankLine = true;
        return false;
      }// else if(pobjEle.LineName == 'Boilerplate' && pobjEle.ItemKey == ''){
    });
    return isBlankLine;
  }

  //Description
  bmlfGetDescription(pobjData) {
    let parrSetKeyData = [
      { KeyCode: 'ITEMKEY', KeyValue1: pobjData.Item_Key, KeyValue2: '', KeyDescription: '' },
      { KeyCode: 'LOCATION', KeyValue1: pobjData.Location, KeyValue2: '', KeyDescription: '' },
      { KeyCode: 'FORMULA', KeyValue1: pobjData.FormulaID, KeyValue2: '', KeyDescription: '' },
    ]
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: parrSetKeyData,
        ScreenName: [{ ScreenName: this.msscreenName }],
        Response: [{ ResponseText: '', ErrorCode: '' }]
      })
    };

    this.userFieldService.bmlfFetchKeyDescription(reqData).subscribe(res => {
      if (res == null) {
        return false;
      }
      if (res.LookupData.length) {
        res.LookupData.forEach((ele) => {
          if (ele.KeyCode == 'ITEMKEY') {
            this.msItemDescription = ele.KeyDescription;
          } else if (ele.KeyCode == 'LOCATION') {
            this.msLocationDescription = ele.KeyDescription;
          } else if (ele.KeyCode == 'FORMULA') {
            this.msFormulaDesc = ele.KeyDescription;
          }
        });
      }
    }, err => {
      console.log(err);
    });
  }

