//---------------------------------------------------------------------------------------
//---------------------------------------------------------------------------------------
bmlfValidateFormulaChange(fld){
  
  if (!CustomValidator.bmgfNullCheckValidator(this.mobjModel.TopAssemblyFrom.trim()) && !CustomValidator.bmgfNullCheckValidator(this.mobjModel.TopAssemblyTo.trim())  ) {
      if(this.mobjModel.TopAssemblyFrom.trim() > this.mobjModel.TopAssemblyTo.trim() ){
          this.bmlfOpenErrorBlock("BOMPURGEPRIORVERSIONS.MSG.BOMCHECK");
          if(fld== "TopAssemblyTo"){
              this.mobjModel.TopAssemblyTo = "";
              this.msTopAssemblyToDescription = "";
              this.mobjModel.LocationTo = "";
              this.msLocationToDescription = ""
          }else{
              this.mobjModel.TopAssemblyFrom = "";
              this.msTopAssemblyFromDescription = "";
              this.mobjModel.LocationFrom = "";
              this.msLocationFromDescription = ""
          }
          return false;
      }
      
  }
}
//---------------------------------------------------------------------------------------
   //---------------------------------------------------------------------------------------
   bmlfProcess(){
    if (this.bmlfValidData() == true){
        this.mbIsLoading = true;
    let reportObj = {};
    reportObj = {
              "ReportName": "BOM Purge Prior Versions Report",
              TopAssemblyFrom: this.mobjModel.TopAssemblyFrom,
              TopAssemblyTo: this.mobjModel.TopAssemblyTo,
              LocationFrom: this.mobjModel.LocationFrom,
              LocationTo: this.mobjModel.LocationTo,
              PurgeDetail: (this.mobjModel.PurgeDetail == 'true' ? true : false),
              PurgeHistory: (this.mobjModel.PurgeHistory == 'true' ? true : false),
              LastNBOMs: (this.bmlfIsEmpty(this.mobjModel.LastNBOMs) ? 0 : this.mobjModel.LastNBOMs),
              ListingFlag: false,
              ListingVariable: ""
    };
        
    
    this.utilService.bmgfReportActionWithOthAPI(reportObj, this.msScreenName,'BOMPurgePriorVersions').then((res: any) => {
        this.mbIsLoading = false;
        if (res != null) {
          if (res.Response[0].ErrorCode == "") {
          }
          else {
            if (res.Response[0].ErrorCode == '2001') {
              this.toastMessageService.notifyError(res.Response[0].ResponseText);
            } else if (res.Response[0].ErrorCode != '' && res.Response[0].ErrorCode != '2001') {
              this.bmlfOpenErrorBlock(res.Response[0].ErrorCode)
            }
            else {
              this.bmlfOpenErrorBlock("No Data Found")
            }
            this.mbIsLoading = false;
          }
        }
      }, error => {
        this.mbIsLoading = false;
      });
    }
    
}
