import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable()
export class BOMEntryService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }
    public bmlfGETGeneralSqlData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GETGeneralSqlData`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfGetDefaultSQlData(reqdata) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GETCustomSqlData`;
        return this.httpExt.post(url, reqdata, null);
    }
    public bmlfGetLookUpData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetLookupData`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfDeleteBOMRecord(reqdata) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFDeleteBomRecord`;
        return this.httpExt.post(url, reqdata, null);
    }
    public bmlfReleaseBOMRecord(reqdata) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFReleaseBOM`;
        return this.httpExt.post(url, reqdata, null);
    }
    public bmlfDeleteBOMLineRecord(reqdata) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFDeleteBomLine`;
        return this.httpExt.post(url, reqdata, null);
    }
    public bmlfGetBOMDetail(reqdata) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetBOMDetail`;
        return this.httpExt.post(url, reqdata, null);
    }
    public bmlfSaveBOMDetail(reqdata, type) {
        if (type == "") {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFSaveBomInfo`;
            return this.httpExt.post(url, reqdata, null);
        } else {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFUpdateBomInfo`;
            return this.httpExt.post(url, reqdata, null);
        }
    }
    public getUomUnit(reqdata) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetItemUOMConversion`;
        return this.httpExt.post(url, reqdata, null);
    }
    public bmlfResequenceBOMRecord(reqdata) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFBOMResequence`;
        return this.httpExt.post(url, reqdata, null);
    }
    public bmlfAlternateItemRecord(reqdata) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFBOMAlternateItem`;
        return this.httpExt.post(url, reqdata, null);
    }
    public bmlfGetBomExplosion(reqdata) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFExplodeBOM`;
        return this.httpExt.post(url, reqdata, null);
    }
    public bmlfuploadBOM(reqdata){
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFExessUploadBOM`;
        return this.httpExt.post(url, reqdata, null);
      }
}