import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { Observable } from 'rxjs';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({
  providedIn: 'root'
})
export class ContainerBomEntryService {
  
  constructor(
    private httpExt: HttpCleintExtService,
    private appSettings: AppSettingsService    
     ) {
  }

  public bmlfonGetList(headerData: any): Observable<ODataModel> {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetLookupData`;
    return this.httpExt.post<ODataModel>(url, headerData, null);
  }

public bmlfonGet(reqdata): Observable<any> {
  const url = `${this.appSettings.ServerAPIEndpointnew}/bme/GetContainerBOM`;
  return this.httpExt.post<any>(url, reqdata, null);
}

  public bmlfonSave(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/CreateContainerBOM`;
    return this.httpExt.post<any>(url, reqdata, null);
  }

  public bmlfonUpdate(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/UpdateContainerBOM`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonDelete(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteContainerBOM`;
    return this.httpExt.post(url, reqdata, null);
  }
  
  public bmlfonDeleteLine(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteContainerBOMLn`;
    return this.httpExt.post(url, reqdata, null);
  }

  public BMGFContainerBOMResequence(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/ContainerBOMResequence`;
    return this.httpExt.post(url, reqdata, null);
  }

  public getUomUnit(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetItemUOMConversion`;
    return this.httpExt.post(url, reqdata, null);
   }




}
